import { Component, OnInit } from '@angular/core';
import {DataService} from '../shared/data.service';
import { SocketService } from '../shared/socket.service';
@Component({
  selector: 'app-manual',
  templateUrl: './manual.component.html',
  styleUrls: ['./manual.component.css']
})
export class ManualComponent implements OnInit {
  private chlist:boolean[] = [true , false, false, false];
  constructor(private socketService:SocketService , private dataService:DataService) { 


  }
  
  ngOnInit() {}

  private CommandGpio(channel:number , cmd:boolean):void {
  	let data = {'channel': channel,'cmd': cmd}
  	this.socketService.socket.emit("REQ_CH" , data);

  }

  private clickChannel(ch:number):void {
    console.log("click channel : " + ch);
    this.chlist = [false, false, false, false];
    this.chlist[ch] = true;
  }

  private pushSwitch(event , ch): void{
    console.log('push switch : ' + ch) + " " + event;
    this.dataService.stageList[ch].stage=event;
    let data = {
      'chid':1,
      'stageList': this.dataService.stageList
    }
    this.dataService.emit('UPDATE_CHANNEL' , data);
  }


}



